/**
 * Scratch Chart Component
 * Line chart showing scratch index progression
 */

import { useMemo } from 'react';
import PropTypes from 'prop-types';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler,
} from 'chart.js';
import { Line } from 'react-chartjs-2';
import styles from './ScratchChart.module.css';

// Register Chart.js components
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler,
);

export function ScratchChart({ data, title = ' ' }) {
  const chartData = useMemo(() => {
    if (!data || data.length === 0) {
      return null;
    }

    const sortedData = [...data].sort((a, b) => a.passes - b.passes);
    
    return {
      labels: sortedData.map((item) => `${item.passes} .`),
      datasets: [
        {
          label: ' ',
          data: sortedData.map((item) => item.scratch_index * 100),
          borderColor: '#00d4ff',
          backgroundColor: 'rgba(0, 212, 255, 0.1)',
          borderWidth: 2,
          pointRadius: 6,
          pointHoverRadius: 8,
          pointBackgroundColor: '#00d4ff',
          pointBorderColor: '#0a1628',
          pointBorderWidth: 2,
          tension: 0.3,
          fill: true,
        },
      ],
    };
  }, [data]);

  const options = useMemo(() => ({
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: {
        display: false,
      },
      title: {
        display: !!title,
        text: title,
        color: '#f1f5f9',
        font: {
          family: "'Inter', sans-serif",
          size: 14,
          weight: '600',
        },
        padding: {
          bottom: 16,
        },
      },
      tooltip: {
        backgroundColor: '#1e293b',
        titleColor: '#f1f5f9',
        bodyColor: '#94a3b8',
        borderColor: '#334155',
        borderWidth: 1,
        cornerRadius: 8,
        padding: 12,
        displayColors: false,
        callbacks: {
          title: (items) => `: ${data[items[0].dataIndex]?.passes || 0}`,
          label: (item) => `: ${item.raw.toFixed(2)}%`,
        },
      },
    },
    scales: {
      x: {
        grid: {
          color: 'rgba(51, 65, 85, 0.5)',
          drawBorder: false,
        },
        ticks: {
          color: '#94a3b8',
          font: {
            family: "'Inter', sans-serif",
            size: 12,
          },
        },
      },
      y: {
        grid: {
          color: 'rgba(51, 65, 85, 0.5)',
          drawBorder: false,
        },
        ticks: {
          color: '#94a3b8',
          font: {
            family: "'Inter', sans-serif",
            size: 12,
          },
          callback: (value) => `${value}%`,
        },
        min: 0,
        suggestedMax: 50,
      },
    },
    interaction: {
      intersect: false,
      mode: 'index',
    },
  }), [title, data]);

  if (!chartData) {
    return (
      <div className={styles.empty}>
        <p>   </p>
      </div>
    );
  }

  return (
    <div className={styles.container}>
      <Line data={chartData} options={options} />
    </div>
  );
}

ScratchChart.propTypes = {
  data: PropTypes.arrayOf(PropTypes.shape({
    passes: PropTypes.number.isRequired,
    scratch_index: PropTypes.number.isRequired,
  })),
  title: PropTypes.string,
};

export default ScratchChart;



