/**
 * Image Carousel Component
 * Display experiment images with navigation
 */

import { useState, useMemo } from 'react';
import PropTypes from 'prop-types';
import { imageService } from '@api';
import styles from './ImageCarousel.module.css';

export function ImageCarousel({ images = [], onImageClick }) {
  const [currentIndex, setCurrentIndex] = useState(0);

  // Sort images by passes
  const sortedImages = useMemo(() => {
    return [...images].sort((a, b) => a.passes - b.passes);
  }, [images]);

  const currentImage = sortedImages[currentIndex];

  const handlePrev = () => {
    setCurrentIndex((prev) => (prev > 0 ? prev - 1 : sortedImages.length - 1));
  };

  const handleNext = () => {
    setCurrentIndex((prev) => (prev < sortedImages.length - 1 ? prev + 1 : 0));
  };

  const handleThumbnailClick = (index) => {
    setCurrentIndex(index);
  };

  const handleKeyDown = (e) => {
    if (e.key === 'ArrowLeft') handlePrev();
    if (e.key === 'ArrowRight') handleNext();
  };

  if (sortedImages.length === 0) {
    return (
      <div className={styles.empty}>
        <svg width="48" height="48" viewBox="0 0 48 48" fill="none" stroke="currentColor" strokeWidth="1.5">
          <rect x="6" y="10" width="36" height="28" rx="4" />
          <circle cx="18" cy="22" r="4" />
          <path d="M42 30l-8-8-12 12-6-6-10 10" strokeLinecap="round" strokeLinejoin="round" />
        </svg>
        <p> </p>
      </div>
    );
  }

  // Create image URL from base64 data
  const getImageUrl = (imageData) => {
    if (!imageData) return '';
    // Check if it's already a data URL
    if (typeof imageData === 'string' && imageData.startsWith('data:')) {
      return imageData;
    }
    // Convert base64 to data URL
    return imageService.createImageUrl(imageData);
  };

  return (
    <div 
      className={styles.container}
      tabIndex={0}
      onKeyDown={handleKeyDown}
    >
      {/* Main image */}
      <div className={styles.mainImage}>
        <img
          src={getImageUrl(currentImage?.image_data)}
          alt={`: ${currentImage?.passes || 0}`}
          onClick={() => onImageClick?.(currentImage)}
        />
        
        {/* Navigation arrows */}
        {sortedImages.length > 1 && (
          <>
            <button 
              className={`${styles.navButton} ${styles.prevButton}`}
              onClick={handlePrev}
              aria-label=""
            >
              <svg width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
                <path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z" />
              </svg>
            </button>
            <button 
              className={`${styles.navButton} ${styles.nextButton}`}
              onClick={handleNext}
              aria-label=""
            >
              <svg width="24" height="24" viewBox="0 0 24 24" fill="currentColor">
                <path d="M8.59 16.59L10 18l6-6-6-6-1.41 1.41L13.17 12z" />
              </svg>
            </button>
          </>
        )}

        {/* Image info overlay */}
        <div className={styles.imageInfo}>
          <span className={styles.passesLabel}>
            {currentImage?.passes === 0 ? '' : `${currentImage?.passes} `}
          </span>
          <span className={styles.counter}>
            {currentIndex + 1} / {sortedImages.length}
          </span>
        </div>
      </div>

      {/* Thumbnails */}
      {sortedImages.length > 1 && (
        <div className={styles.thumbnails}>
          {sortedImages.map((image, index) => (
            <button
              key={image.id}
              className={`${styles.thumbnail} ${index === currentIndex ? styles.active : ''}`}
              onClick={() => handleThumbnailClick(index)}
              aria-label={` ${index + 1}`}
            >
              <img
                src={getImageUrl(image.image_data)}
                alt={`: ${image.passes}`}
              />
              <span className={styles.thumbnailLabel}>
                {image.passes === 0 ? '.' : image.passes}
              </span>
            </button>
          ))}
        </div>
      )}
    </div>
  );
}

ImageCarousel.propTypes = {
  images: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.string.isRequired,
    passes: PropTypes.number.isRequired,
    image_data: PropTypes.string,
  })),
  onImageClick: PropTypes.func,
};

export default ImageCarousel;



