/**
 * Admin Page
 * Administration panel for managing users, films, and configurations
 */

import { useState } from 'react';
import { Layout } from '@components/layout';
// Button and Card imports available if needed
import { UsersManagement } from './UsersManagement';
import { FilmsManagement } from './FilmsManagement';
import { ConfigsManagement } from './ConfigsManagement';
import styles from './AdminPage.module.css';

const TABS = [
  { id: 'users', label: '', icon: '' },
  { id: 'films', label: ' ', icon: '' },
  { id: 'configs', label: '', icon: '' },
];

export function AdminPage() {
  const [activeTab, setActiveTab] = useState('users');

  const renderContent = () => {
    switch (activeTab) {
      case 'users':
        return <UsersManagement />;
      case 'films':
        return <FilmsManagement />;
      case 'configs':
        return <ConfigsManagement />;
      default:
        return null;
    }
  };

  return (
    <Layout>
      <div className={styles.page}>
        <div className={styles.header}>
          <h1 className={styles.title}>-</h1>
          <p className={styles.subtitle}>
             ,     
          </p>
        </div>

        {/* Tabs */}
        <div className={styles.tabs}>
          {TABS.map((tab) => (
            <button
              key={tab.id}
              className={`${styles.tab} ${activeTab === tab.id ? styles.active : ''}`}
              onClick={() => setActiveTab(tab.id)}
            >
              <span className={styles.tabIcon}>{tab.icon}</span>
              {tab.label}
            </button>
          ))}
        </div>

        {/* Content */}
        <div className={styles.content}>
          {renderContent()}
        </div>
      </div>
    </Layout>
  );
}

export default AdminPage;

